import React, { useState } from "react";
import Entry from "./Entry";
import emojipedia from "../emojipedia";

function Books() {
  const [input, setInput] = useState("");
  function Filt(event){
    setInput(event.target.value);
  }
  
  
  return (
    <>
      <div className="container">
        <input placeholder="Search book name" onChange={Filt} type="text" />
        <div className="bookcontainer">
          {
            emojipedia.filter((items)=>{
              if(input==""){
                return <Entry key={items.id} id = {items.id} author = {items.author} name = {items.name} publication = {items.pub} />
              }
              else if(items.name.toLowerCase().includes(input.toLowerCase())){
                return <Entry key={items.id} id = {items.id} author = {items.author} name = {items.name} publication = {items.pub} />
              }
            }).map((items)=>{
              return <Entry key={items.id} id = {items.id} author = {items.author} name = {items.name} publication = {items.pub} />
            })
          }
        </div>
      </div>
    </>
  );
}

export default Books;
