import React from "react";

function Entry(props) {
  return (
    <div className="books">
      <div>
        <h2>
          {props.id}) {props.name}
        </h2>
        <h3>{props.author}</h3>
        <p>{props.publication}</p>
      </div>
    </div>
  );
}

export default Entry;
